import 'ScssComponents/roasting-times.scss';
import 'ScssComponents/universal/slider-controls.scss';
import Swiper, { Navigation } from 'swiper';
import { exist, onWindowResize, getWindowSize } from '../utils';
import { BREAKPOINTS } from '../utils/constants';
import 'swiper/css';

const roastingTimes = (
	sliderSelector = '.js-roasting-times-slider',
	btnNextSelector = '.js-roasting-times-next-button',
	btnPrevSelector = '.js-roasting-times-prev-button',
) => {
	Swiper.use([Navigation]);

	const $slider = document.querySelector(sliderSelector);
	const $btnNext = document.querySelector(btnNextSelector);
	const $btnPrev = document.querySelector(btnPrevSelector);

	if (!exist([$slider, $btnNext, $btnPrev])) return;
	let roastingSwiper;

	const initSwiper = () => {
		return new Swiper($slider, {
			speed: 800,
			loop: false,
			navigation: {
				nextEl: $btnNext,
				prevEl: $btnPrev,
			},
			slidesPerView: 2.06,
		});
	};

	onWindowResize(() => {
		if (getWindowSize().windowWidth < BREAKPOINTS.mediaPoint1) {
			if (!roastingSwiper) {
				roastingSwiper = initSwiper();
			}
		} else {
			if (roastingSwiper) {
				roastingSwiper.destroy();
				roastingSwiper = null;
			}
		}
	});
};

export default roastingTimes;
