import Swiper, { Navigation, EffectFade, Thumbs } from 'swiper';
import { buildSwiper, removeSwiper } from './build-swiper';
import 'ScssComponents/cooking-tabs.scss';
import { exist } from '../utils';
import tabsNavigation from './tabs-navigation';

import 'swiper/css';
// import 'swiper/scss/scrollbar';
import 'swiper/css/effect-fade';
import 'swiper/css/thumbs';

const cookingTabs = ({
	sectionSelector = '.js-cooking-tabs',
	btnNext = '.js-cooking-tab-next-button',
	btnPrev = '.js-cooking-tab-prev-button',
	content = '.js-cooking-tabs-content',
	nav = '.js-cooking-tabs-nav',
}) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	$sections.forEach($section => {
		const $slider = $section.querySelector(content);
		const $nav = $section.querySelector(nav);
		const $prevArrow = $section.querySelector(btnPrev);
		const $nextArrow = $section.querySelector(btnNext);

		if (!exist([$prevArrow, $nextArrow, $slider, $nav])) return;

		Swiper.use([Navigation, Thumbs, EffectFade]);

		const thumbsInstance = new Swiper($nav, {
			speed: 400,
			navigation: {
				prevEl: $prevArrow,
				nextEl: $nextArrow,
			},
			slidesPerView: 'auto',
			watchSlidesProgress: true,
			centeredSlides: true,
			breakpoints: {
				1023: {
					centeredSlides: false,
				},
			},
		});

		const sliderInstance = new Swiper($slider, {
			slidesPerView: 1,
			autoHeight: true,
			// calculateHeight: true,
			speed: 400,
			thumbs: {
				swiper: thumbsInstance,
			},
			navigation: {
				prevEl: $prevArrow,
				nextEl: $nextArrow,
			},
			effect: 'fade',
			fadeEffect: {
				crossFade: true,
			},
		});
	});
};

export default cookingTabs;
