import { exist } from '../utils';

const tabsNavigation = ({
	sectionSelector,
	link,
	block,
	classLink,
	classContent,
	btnNext,
	btnPrev,
}) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	$sections.forEach($section => {
		const activeLinkClass = `${classLink.slice(1)}--active_state`;
		const activeContentClass = `${classContent.slice(1)}--active_state`;
		const $linkSelector = $section.querySelectorAll(link);
		const $blockSelector = $section.querySelectorAll(block);

		const $btnNext = $section.querySelector(btnNext);
		const $btnPrev = $section.querySelector(btnPrev);
		if (!exist([$btnNext, $btnPrev])) return;

		let activeTab = 0;
		let arrayTabs = {};

		if ($linkSelector.length && $blockSelector.length) {
			$linkSelector.forEach((item) => {
				const content = document.querySelector(`[data-tab-content = '${item.dataset.tab}']`);
				if (!content) return;
				arrayTabs[item.dataset.tab] = {
					trigger: item,
					content,
				};
			});

			const addActiveState = () => {
				arrayTabs[activeTab]?.trigger?.classList.add(activeLinkClass);
				arrayTabs[activeTab]?.content?.classList.add(activeContentClass);
			};

			const removeActiveState = () => {
				arrayTabs[activeTab]?.trigger?.classList.remove(activeLinkClass);
				arrayTabs[activeTab]?.content?.classList.remove(activeContentClass);
			};

			$btnNext.addEventListener('click', () => {
				removeActiveState();
				activeTab = activeTab >= Object.values(arrayTabs).length - 1 ? 0 : activeTab + 1;
				addActiveState();
			});

			$btnPrev.addEventListener('click', () => {
				removeActiveState();
				activeTab = activeTab === 0 ? Object.values(arrayTabs).length - 1 : activeTab - 1;
				addActiveState();
			});

			$section.addEventListener('click', (e) => {
				e.preventDefault();
				const { dataset } = e.target;
				if (dataset && dataset.tab) {
					removeActiveState();
					activeTab = Number(dataset.tab);
					addActiveState();
				}
			});
		}
	});
};

export default tabsNavigation;
