import productSlider from 'components/products-slider';
import textSection from 'components/text-section';
import cookingInfoList from 'components/cooking-info-list';
import cookingDetails from 'components/cooking-details';
import cookingTabs from 'components/cooking-tabs';
import roastingTimes from 'components/roasting-times';

const ourProductsWagyuCookingTips = () => {
	textSection();
	cookingInfoList();
	productSlider();
	cookingDetails();
	cookingTabs({
		sectionSelector: '.js-cooking-tabs',
		btnNext: '.js-cooking-tab-next-button',
		btnPrev: '.js-cooking-tab-prev-button',
		content: '.js-cooking-tabs-content',
		nav: '.js-cooking-tabs-nav',
	});
	roastingTimes();
};

export default ourProductsWagyuCookingTips;
